import React from 'react';

class ConsoleSequence extends React.Component {
  constructor(props) {
    super(props);
    this.orderedChildren = React.Children.map(this.props.children, (child, index) => {
      let dependencies = (this.props.dependencyMap)
                         ? this.props.dependencyMap[index]
                         : (index > 0) ? [index-1] : [];
      return React.cloneElement(child, {id: index,
                                        key: index,
                                        dependentOn: dependencies,
                                        onComplete: () => this.notifyNext(index)
                                       });
    });
    this.state = {};
  }

  notifyNext(index) {
    this.setState({[index]: true},
      (index+1 === this.orderedChildren.length) ? this.props.onComplete : undefined
    );
    window.scrollTo(0,document.body.scrollHeight);
  }

  render() {
    return (
      <div>
        {this.orderedChildren.map(child => {
          let visible = true;
          for (var p of child.props.dependentOn) {
            visible = visible && this.state[p];
          }
          if (visible) {
            return child;
          } else {
            return <div className="inline"></div>
          }
        })}
      </div>
    )
  }
}

export default ConsoleSequence;
