import React from 'react';

class CharByChar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      currentIndex: (this.props.letterDelay <= 0) ? this.props.children.length : 1,
      complete: false
    };
  }

  componentDidMount() {
    this.timerID = setInterval(
      () => this.incrementIndex(), this.props.letterDelay
    );
  }

  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  incrementIndex() {
    let textLength = this.props.children.length;
    if (this.state.currentIndex >= textLength) {
      clearInterval(this.timerID);
      this.setState({complete: true}, this.props.onComplete);
    } else {
      this.setState((state) => ({
        currentIndex: state.currentIndex+1
      }));
    }
  }

  render() {
    return(
      <p className={this.props.className}>
        {this.props.children.substring(0, this.state.currentIndex)}
      </p>
    );
  }
}

export default CharByChar;
